import { request, requestDownload } from 'utils';

const prefix = '/api/students';

export const getSubsidizeStudents = ({
  studyFieldId,
  trainTypeId,
  facultyId,
  courseId,
  classId,
  semesterId,
  yearEduId,
  policyObjectId,
  studentCode,
  fullName,
  page,
  pageSize = 10,
  orderBy,
  isPagingEnabled = true,
}) =>
  request({
    prefix,
    url: `/Subsidize_Student`,
    method: 'GET',
    params: {
      studyFieldId,
      trainTypeId,
      facultyId,
      courseId,
      classId,
      semesterId,
      yearEduId,
      policyObjectId,
      studentCode,
      fullName,
      page,
      pageSize,
      orderBy,
      isPagingEnabled,
    },
    data: {},
  });

export const createSubsidizeStudents = data =>
  request({
    prefix,
    url: `/Subsidize_Student`,
    data,
    method: 'POST',
  });

export const deleteSubsidizeStudents = data =>
  request({
    prefix,
    params: {},
    url: `/Subsidize_Student`,
    data,
    method: 'DELETE',
  });

export const getEnrollSolutionsById = id =>
  request({
    prefix,
    url: `/Subsidize_Student/${id}`,
    method: 'GET',
  });

export const updateSubsidizeStudents = ({
  id,
  studentId,
  subsidizeObjectId,
  semesterId,
  yearEduId,
}) =>
  request({
    prefix,
    url: `/Subsidize_Student/${id}`,
    data: {
      id,
      studentId,
      subsidizeObjectId,
      semesterId,
      yearEduId,
    },
    method: 'PUT',
  });

export const exportSubsidizeStudents = ({
  trainTypeId,
  facultyId,
  courseId,
  studyFieldId,
  classId = null,
  semesterId,
  yearEduId,
  studentCode = '',
  fullname = '',
}) =>
  requestDownload({
    defaultFileName: 'File_SubsidizeStudents.xlsx',
    prefix,
    url: `/Subsidize_Student/ExportSubsidizeStudents`,
    data: {
      trainTypeId,
      facultyId,
      courseId,
      studyFieldId,
      classId,
      semesterId,
      yearEduId,
      studentCode,
      fullname,
    },
    method: 'POST',
  });



