import React, { Component, Fragment } from 'react';
import Page from 'components/LayoutComponents/Page';
import { Card, Row, Form, Col, Modal, Table, message } from 'antd';
import { TableData, SelectAjax, YearSemesterInput, CartHeadAction } from 'components/common/index';
import { getQueryParams, udpateQueryParams } from 'utils/urlUtil';
import {
  getSubsidizeStudents,
  createSubsidizeStudents,
  deleteSubsidizeStudents,
  exportSubsidizeStudents,
} from 'services/subsidyStudent';
import BtnExport from 'components/common/button/BtnExport';
import { getSubsidizeObjects } from 'services/subsidyObjectForSubsidyStudent';
import { dateToString } from 'utils/formatUtil';
import { requiredRule } from 'utils/validateRules';
import { BtnSave, BtnCancel } from 'components/common/button/index';
import SearchBar from './SearchBar';
import './style.scss';

@Form.create({ name: 'formListSubsidyObject' })
class ListSubsidyObject extends Component {
  state = {
    reload: 0,
    paramSearch: {},
    visible: false,
    type: '',
    enableBtnCancelObject: '',
    hasRowSelected: '',
    selectedRowKeys: [],
    selectedRowKeysPage: [],
    dataSelectedTable: [],
    dataForSetSubsidyStudents: [],
  };

  componentDidMount = () => {
    this.handleChangeQueryParams();
  };

  componentWillReceiveProps = () => {
    this.handleChangeQueryParams();
  };

  onSelectTableChange = async (selectedRowKeys, selectedRows) => {
    await this.setState({ selectedRowKeys, selectedRowKeysPage: [...selectedRowKeys] });
    await this.setState({ dataSelectedTable: selectedRows });
    const hasRowSelected = selectedRowKeys.length > 0;
    this.setState({ hasRowSelected });
    this.enableBtnCancelObject();
  };

  onSelectModalChange = async (selectedRowKeys, selectedRows) => {
    await this.setState({
      dataForSetSubsidyStudents: selectedRows,
      selectedRowKeys,
    });
  };

  setFieldsYearSemesterInput = () => {
    const { paramSearch } = this.state;
    const { form } = this.props;
    form.setFieldsValue({ semesterId: paramSearch.semesterId, yearEduId: paramSearch.yearEduId });
  };

  enableBtnCancelObject = async () => {
    const { dataSelectedTable } = this.state;
    const dataHasSubsidy = await dataSelectedTable.filter(item => item.subsidizeObjectId !== null);
    const dataNotSubsidy = await dataSelectedTable.filter(item => item.subsidizeObjectId === null);
    if (dataHasSubsidy.length > 0 && dataNotSubsidy.length === 0) {
      this.setState({ enableBtnCancelObject: '1' });
    }
    if (dataNotSubsidy.length !== 0) {
      this.setState({ enableBtnCancelObject: '' });
    }
  };

  handleChangeQueryParams = () => {
    this.setState({ paramSearch: getQueryParams() });
  };

  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (err) {
        return;
      }
      udpateQueryParams(values);
    });
  };

  showModal = type => {
    this.setState(
      {
        visible: true,
        type,
        selectedRowKeys: [],
      },
      () => {
        this.setFieldsYearSemesterInput();
      }
    );
  };

  cancelDataPolicyStudents = () => {
    const { dataSelectedTable } = this.state;
    const data = dataSelectedTable
      .filter(item => item.subsidizeObjectId !== null)
      .map(item => {
        const { studentId, semesterId, yearEduId, subsidizeObjectId } = item;
        return { studentId, semesterId, yearEduId, subsidizeObjectId };
      });
    this.deleteSubsidizeStudent(data);
  };

  deleteSubsidizeStudent = async data => {
    try {
      await deleteSubsidizeStudents(data);
      message.success('Xóa thành công');
      this.setState({
        reload: this.state.reload + 1,
        selectedRowKeys: [],
        selectedRowKeysPage: [],
        hasRowSelected: '',
        enableBtnCancelObject: '',
      });
    } catch (error) {
      console.log(error);
    }
  };

  handleCancel = e => {
    console.log(e);
    this.setState({
      visible: false,
      selectedRowKeys: [...this.state.selectedRowKeysPage],
    });
  };

  handleOk = () => {
    this.props.form.validateFields((err, values) => {
      if (err) {
        return;
      }
      const { dataForSetSubsidyStudents: dataForSetPolicyStudents } = this.state;
      const data = dataForSetPolicyStudents.map(item => {
        const { studentId } = item;
        return { studentId, ...values };
      });
      if (data.length === 0) {
        message.warning('Vui lòng chọn sinh viên!');
      }
      if (data.length > 0) {
        this.saveDataSubsidizeStudents(data);
      }
    });
  };

  saveDataSubsidizeStudents = async data => {
    try {
      await createSubsidizeStudents(data);
      message.success('Thêm thành công!');
      this.setState({
        visible: false,
        reload: this.state.reload + 1,
        selectedRowKeys: [],
        selectedRowKeysPage: [],
        dataForSetSubsidyStudents: [],
        dataSelectedTable: [],
        hasRowSelected: '',
        enableBtnCancelObject: '',
      });
    } catch (error) {
      console.log(error);
    }
  };

  handleExport = async ({
    trainTypeId,
    facultyId,
    courseId,
    studyFieldId,
    classId,
    semesterId,
    yearEduId,
    studentCode,
    studentFullName,
  }) => {
    try {
      await exportSubsidizeStudents({
        trainTypeId,
        facultyId,
        courseId,
        studyFieldId,
        classId,
        semesterId,
        yearEduId,
        studentCode,
        studentFullName,
      });
    } catch (error) {
      console.log(error);
    }
  };

  render() {
    const { selectedRowKeys } = this.state;
    this.columns = [
      {
        title: 'Mã SV',
        dataIndex: 'code',
        key: 'code',
      },
      {
        title: 'Họ tên',
        dataIndex: 'fullname',
        key: 'fullname',
      },
      {
        title: 'Ngày sinh',
        dataIndex: 'dob',
        key: 'dob',
        render: (text, record) => dateToString(record.dob, 'DD/MM/YYYY'),
      },
      {
        title: 'Lớp',
        dataIndex: 'className',
        key: 'className',
      },
      {
        title: 'Tên đối tượng ',
        dataIndex: 'subsidizeObjectLabel',
        key: 'subsidizeObjectLabel',
      },
    ];

    const rowSelectedOfDataSearchTable = {
      selectedRowKeys,
      onChange: this.onSelectTableChange,
    };

    const rowSelectedSetPolicyStudents = {
      selectedRowKeys,
      onChange: this.onSelectModalChange,
    };

    const { getFieldDecorator } = this.props.form;
    const { paramSearch = {} } = this.state;
    const listParamSearch = this.state.paramSearch;
    return (
      <Fragment>
        <Card title="Chọn đối tượng chính sách">
          <CartHeadAction>
            <BtnExport
              title="Xuất Excel"
              onClick={() =>
                this.handleExport({
                  trainTypeId: listParamSearch.trainTypeId,
                  facultyId: listParamSearch.facultyId,
                  courseId: listParamSearch.courseId,
                  studyFieldId: listParamSearch.studyFieldId,
                  yearEduId: listParamSearch.yearEduId,
                  semesterId: listParamSearch.semesterId,
                  studentCode: listParamSearch.studentCode,
                  studentFullName: listParamSearch.studentFullName,
                })
              }
            />
          </CartHeadAction>
          <Row>
            <Col span={24}>
              <SearchBar />
            </Col>
          </Row>
          <Row>
            <Col span={24}>
              <TableData
                rowSelection={rowSelectedOfDataSearchTable}
                reload={this.state.reload}
                paramSearch={this.state.paramSearch}
                allowGetData={
                  !!paramSearch.trainTypeId &&
                  !!paramSearch.facultyId &&
                  !!paramSearch.courseId &&
                  !!paramSearch.studyFieldId &&
                  !!paramSearch.semesterId &&
                  !!paramSearch.yearEduId
                }
                service={getSubsidizeStudents}
                columns={this.columns}
                rowKey="id"
              />
            </Col>
            <Col span={24} style={{ textAlign: 'center' }}>
              <div className="btn-group">
                <BtnSave
                  onClick={() => this.showModal('set')}
                  title="Xét đối tượng"
                  disabled={!this.state.hasRowSelected}
                />
                <BtnCancel
                  onClick={() => this.cancelDataPolicyStudents()}
                  title="Hủy đối tượng"
                  disabled={!this.state.enableBtnCancelObject}
                />
              </div>
            </Col>
          </Row>
          <Modal
            style={{ top: 20 }}
            width="80%"
            title={this.state.type === 'set' ? 'Xét đối tượng' : 'Hủy đối tượng'}
            visible={this.state.visible}
            forceRender
            onCancel={this.handleCancel}
            footer={[
              <BtnSave onClick={this.handleOk} />,
              <BtnCancel onClick={this.handleCancel} />,
            ]}
          >
            <Form className="form-inline-label" layout="vertical" onSubmit={this.handleSearch}>
              <Col span={24}>
                <YearSemesterInput form={this.props.form} disabled />
                <Form.Item label="ĐTTC">
                  {getFieldDecorator(`subsidizeObjectId`, {
                    // initialValue: 1,
                    rules: [requiredRule],
                  })(<SelectAjax service={getSubsidizeObjects} />)}
                </Form.Item>
              </Col>
              <Col span={24}>
                <Table
                  rowSelection={rowSelectedSetPolicyStudents}
                  dataSource={this.state.dataSelectedTable}
                  columns={this.columns}
                  pagination={false}
                  rowKey="id"
                />
              </Col>
            </Form>
          </Modal>
        </Card>
      </Fragment>
    );
  }
}

export default props => (
  <Page {...props}>
    <ListSubsidyObject />
  </Page>
);



